.Prod-Detail {
    padding-top: 80px;
    width: 100vw;
    height: 100vh;
}

.prod-container {
    padding: 3rem;
    width: 100%;
    height: 100%;
    display: flex;
   justify-content: space-evenly;
}

.prod-img {
    width: 30%;
    height: 100%;
}

.prod-img>img {
    width: 100%;
    height: 80%;
    object-fit: cover;
}

.prod-data {
    width: 60%;
    height: 100%;
    padding: 2rem;
}

.prod-data>h1 {
    font-size: 3rem;
}

.prod-data>p {
    font-size: 1rem;
    padding-bottom: 1rem;
    color: black;
}

.prod-description {
    color: grey;
}

.prod-details-btn {
    display: flex;
    align-items: flex-start;
    gap: 2rem;
    margin-top: 0.5rem;
}

.btn-1 {
    border: 1px solid black;
    padding: 1rem 2rem 1rem 2rem;
}

.btn-1:hover {
    background: #102833;
    color: white;
    transition: 0.2s ease-in-out;
}

.btn-2 {
    padding: 1rem 2rem 1rem 2rem;
    background: #102833;
    color: white;
}